.pulse {
  position: relative;
  left: 50%;
  top: 50vh;
  margin-left: -40px;
  margin-top: -40px;
}
.pulse:before,
.pulse:after {
  content: '';
  border: 5px solid #323232;
  width: 80px;
  height: 80px;
  border-radius: 500px;
  position: absolute;
}
.pulse:before {
  animation: pulse-outer 0.8s ease-in infinite;
}
.pulse:before {
  animation: pulse-inner 0.8s linear infinite;
}
@keyframes pulse-outer {
  0% {
    opacity: 1;
    filter: alpha(opacity=100);
  }
  50% {
    opacity: 0.5;
    filter: alpha(opacity=50);
  }
  100% {
    opacity: 0;
    filter: alpha(opacity=0);
  }
}
@keyframes pulse-inner {
  0% {
    opacity: 0;
    filter: alpha(opacity=0);
    transform: scale(0);
  }
  100% {
    opacity: 1;
    filter: alpha(opacity=100);
    transform: scale(1);
  }
}
